html, body, #root, .App, #home-page {
  height: 100%;
  margin: 0;
}



.App-header {
  background-color: white;
  min-height: 100vh;
  display: flex;
  font-size: calc(10px + 2vmin);
  color: black;
  font-family: "Lucida Console", "Courier New", monospace;
  /* text-align: center; */
  padding-left: 5%;
}

#flexDiv {
  flex-direction: column;
}

i {
  font-size: 200%;
}

#NavBar-page {
  display: flex;
  padding-right: 2%;
  padding-top: 2%;
  font-family: "Lucida Console", "Courier New", monospace;
  padding-bottom: 50px;
}

.navBarLinks {
  margin-right: 3%;
  text-decoration: none;
  color: black;
  margin: auto;
}

.navBarLinks:hover {
  color: #FF8000;
  text-decoration: underline;
}

#navBar {
  padding-bottom: 2%;
  text-align: center;
}

hr {
  padding-left: 25%;
  text-align: left;
  width: 50%;
}

input {
  height: 50px;
  width: 80%;
  /* margin-bottom: 10%; */
  margin-top: 5%;
  border: none;
  border-bottom: 2px solid black ;
  font-size: 45px;
  font-family: "Lucida Console", "Courier New", monospace;
}


button {
  width: 36%;
}


@media only screen and (max-width: 500px) {
  .picAndBlurb, .payment {
    display: block;
    margin-left: 15%;
    margin-right: 5%;
  }

  #purchasePic {
    margin-left: 15%;
    width: 50%;
  }

  #C12Logo {
    width: 50%;
    height: 1%;
  }

  #paypal{
    width: 80%;
    left: 0;
  }

  .aboutText{
    padding-left: 10%;
    padding-right: 10%;

  }

  #teamPic {
    width: 100%;
    height: 1%;
  }

  .rules{
    left: 0;
  }
}